                ...referenceANDtypeDocumentLie(a).reduce((obj, e, i) => {
                    if(i%2 === 0) obj[`REFERENCE DOCUMENT LIE ${(i+1)-(i/2)}`] = e;
                    else obj[`TYPE DOCUMENT LIE ${(i+1)/2}`] = e;
                    return obj;
                }, {}),
                "NUMERO AMENDEMENT": "\"to be filled\"", // DEFAULT
                "DATE AMENDEMENT": "\"to be filled\"", // DEFAULT
                "NUMERO REVISION": this.numRevisionValue,
                "DATE REVISION": this.dateRevisionValue,
                "TYPE DE MODIFICATION": typeModification(a),
                "COMMENTS": endTableCommentsForSpecificCases(a, this.optimized),
              }
            );
          };
        });
      });

      const download_worksheetFH = XLSX.utils.json_to_sheet(result[0]);
      const download_worksheetFC = XLSX.utils.json_to_sheet(result[1]);

      this.download_workbook = XLSX.utils.book_new();

      this.download_workbook.SheetNames.push("MPD FH induction AMASIS");
      this.download_workbook.Sheets["MPD FH induction AMASIS"] = download_worksheetFH;
      this.download_workbook.SheetNames.push("MPD FC induction AMASIS");
      this.download_workbook.Sheets["MPD FC induction AMASIS"] = download_worksheetFC;
    },
    downloadFile() {
      let name = "file";
      XLSX.writeFile(this.download_workbook, `${name}.xlsx`);
    }
  },
  watch: {
    optimized() {
      this.download_workbook = "";
    },
    workbook() {
      this.download_workbook = "";
    }
  },
}
</script>

