import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue';
import MPDTransformationView from '../views/MPDTransformationView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/MPD-transformation',
    name: 'MPD-transformation',
    component: MPDTransformationView
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
