const referenceCarte = function(item) {
    let element = item["TASK NUMBER"]
    if(element === undefined) return "";
    element = element.replaceAll("\n", "");
    return element;
}

const specialite = function(item) {
 let element = item["SKILL CODE"];
 if(element === undefined) return "";
 return element.substring(0, 3);
}

const controle = function(item) {
    let element = item["SOURCE"];
    if(element === undefined) return "";
    if(element.toUpperCase().match(/5|8|CMR|ALI|FAL/)) return "O";
    return "N";
}

const report = function(item) {
    let element = item["SOURCE"];
    if(element === undefined) return "";
    if(element.toUpperCase().match(/\*|ALI|FAL|CMR/)) return "N";
    return "Y";
}

const outOfPhase = function(value) {
    // if(value.toUpperCase().match(/TCC|WY|DY|PF|EC/)) return "N";
    if(value.toUpperCase().match(/TCC/)) return "N";
    else if(value.toUpperCase().match(/NT/)) return "Y";
    return "\"to be filled\"";
};

const mrb = function(item) {
    let element = item["SOURCE"];
    if(element === undefined) return "";

    if(element.match(/\*\*/)) {
        const arr = ["9", "8", "7", "6", "5"].filter(a => element.match(a));
        if(arr.length > 0) return arr[0]+"**";
        return "**";
    }

    if(element.match(/\*/)) {
        const arr = ["9", "8", "7", "6", "5"].filter(a => element.match(a));
        if(arr.length > 1) return arr[1]+arr[0]+"*";
        if(arr.length > 0) return arr[0]+"*";
        return "*";
    }

    if(element.match(/FAL/)) {
        return "FAL";
    }

    if(element.match(/ALI/)) {
        return "ALI";
    }

    if(element.match(/9|8|7|6|5/)) {
        const arr = ["9", "8", "7", "6", "5"].filter(a => element.match(a));
        if(arr.length > 2) return arr[2]+arr[1]+arr[0];
        if(arr.length > 1) return arr[1]+arr[0];
        return arr[0];
    }

    if(element.match(/MRB/)) {
        return "MRB";
    }

    if(element.match(/CMP/)) {
        return "CMP";
    }

    if(element.match(/LUR/)) {
        return "LUR";
    }

    if(element.match(/SEMR/)) {
        return "SEM";
    }

    if(element.match(/CPCP/)) {
        return "CPC";
    }

    if(element.match(/VSB/)) {
        return "VSB";
    }
}

const codeTache = function(item) {
    let element = item["TASK CODE"];
    if(element === undefined) return "";
    return element.toUpperCase().substring(0, 3);
}

const noteThreshold = function(item, optimized="default") {
    let element;
    if(optimized === "default") element = item["100% THRESHOLD"];
    else if(optimized === "FH") element = item["100% THRESHOLD FH"];
    else if(optimized === "FC") element = item["100% THRESHOLD FC"];
    if(element === undefined) return "";
    if(element.toUpperCase().match(/NOTE/)) return "NOTE";
    return "";
}

const compteurSeuil1 = function(item, sample=false, optimized="default") {
    let element;

    if(optimized === "default") {
        if(sample) element = item["SAMPLE THRESHOLD"];
        else element = item["100% THRESHOLD"];
    }
    else if(optimized === "FH") {
        if(sample) element = item["SAMPLE THRESHOLD FH"];
        else element = item["100% THRESHOLD FH"];
    }
    else if(optimized === "FC") {
        if(sample) element = item["SAMPLE THRESHOLD FC"];
        else element = item["100% THRESHOLD FC"];
    };
    
    if(element === undefined) return "";
    if(element.match(/FH/)) return "FH";
    return "";
}

const valeurSeuil1 = function(item, sample=false, optimized="default") {
    let element;
    
    if(optimized === "default") {
        if(sample) element = item["SAMPLE THRESHOLD"];
        else element = item["100% THRESHOLD"];
    }
    else if(optimized === "FH") {
        if(sample) element = item["SAMPLE THRESHOLD FH"];
        else element = item["100% THRESHOLD FH"];
    }
    else if(optimized === "FC") {
        if(sample) element = item["SAMPLE THRESHOLD FC"];
        else element = item["100% THRESHOLD FC"];
    };
    
    if(element === undefined) return "";
    let elements = element.split("OR");
    let results = elements.filter(e => e.match(/FH/));
    if(results.length == 0) return "";
    let result = results[0].replaceAll("\n", "").split(" ")[0];
    return result.length > 6 ? result.substring(0, 6) : result;
}

