const codeCheck = function(item, optimized="default") {
    const tciElement = item["TCI"];
    if(tciElement !== undefined) {
        if(tciElement.match(/T/)) return "TCC";
    };
    let thresholdElement;
    if(optimized === "default") thresholdElement = item["100% THRESHOLD"];
    else if(optimized === "FH") thresholdElement = item["100% THRESHOLD FH"];
    else if(optimized === "FC") thresholdElement = item["100% THRESHOLD FC"];
    if(thresholdElement !== undefined) {
        if(thresholdElement.match(/NOTE/) &&
        thresholdElement.match(/FH|FC|DY|YE|MO/)) return "NT";
    }
    let intervalElement;
    if(optimized === "default") intervalElement = item["100% INTERVAL"];
    else if(optimized === "FH") intervalElement = item["100% INTERVAL FH"];
    else if(optimized === "FC") intervalElement = item["100% INTERVAL FC"];
    if(intervalElement !== undefined) {
        if(intervalElement.match(/NOTE/) &&
        intervalElement.match(/FH|FC|DY|YE|MO/)) return "NT";
    }
    return "\"To be filled\"";
}

const acZone = function(item) {
    let element = item["ZONE"];
    if(element === undefined) return "";
    let elements = element.split("\n");
    while(elements.length < 22) elements.push("");
    return elements;
}

const mpdAccess = function(item) {
    let element = item["ACCESS"];
    if(element === undefined) return "";
    element = element.replaceAll("\n", " ");
    let elements = element.split(" ").filter(e => e != "");
    if(elements.length >= 50) return elements.slice(0, 50);
    while(elements.length < 50) elements.push("");
    return elements;
}

const chargeHh = function(item) {
    let element = item["TASKM.H."];
    if(element === undefined) return "";
    if(element.match(/N\/A|TBC/)) return "";
    if(element == "") return "";
    return element.split("\n")[0].replaceAll(".",",");
}

const nombre = function(item) {
    let element = item["MEN"];
    if(element === undefined) return "";
    return element.split("\n")[0];
}

const libelleCarte = function(item) {
    let element = item["DESCRIPTION"];
    if(element === undefined) return "";
    let result = element.split("\n")[0];
    while(result.length > 40) result = result.split(" ").slice(0,-1).join(" ");
    return result.toUpperCase();
}

const texte = function(item) {
    let element = item["DESCRIPTION"];
    let preparation = item["PREPARATION"];
    let applicability = item["APPLICABILITY"];
    if(preparation !== "") element+="\nPREPARATION\n"+preparation;
    if(applicability !== "") element+="\nEFFECTIVITY\n"+applicability;
    if(element === undefined) return "";
    element = element.replaceAll("\\", "");

    let result = element.split("\n").filter((e, i) => e != "" && i !== 0);
    for(let i = 0; i < result.length; i++) {
        if(result[i].length > 75) {
            let splitE = result[i].split(" ");
            result = [
                ...result.slice(0, i),
                splitE.slice(0, Math.round(splitE.length / 2)).join(" "),
                splitE.slice(Math.round(splitE.length / 2)).join(" "),
                ...result.slice(i+1),
            ];
            i = 0;
        }
    }

    if(result.length == 16) return result;

    if(result.length < 16) {
        while(result.length < 16) result.push("");
        return result;
    }
    

    result = [""];
    let words = element.split("\n").join(" ").replaceAll("  ", " ").replaceAll("  ", " ").split(" ");
    for(let w of words) {
        if((result[result.length - 1].length + w.length + 1) <= 75) result[result.length - 1] += `${w} `;
        else {
            result[result.length - 1] = result[result.length - 1].trim()
            result.push(`${w} `);
        }
    }
    
    if(result.length == 16) return result;

    if(result.length < 16) {
        while(result.length < 16) result.push("");
        return result;
    }

    return result.slice(0, 16);
}

const ata = function(item) {
    let element = item["TASK NUMBER"];
    if(element === undefined) return "";
    return element.substring(0, 2);
}

const getTypeDocumentLie = function(reference) {
    if(reference.toLowerCase().match(/appendix|apx/)) return "MPD";
    else if(reference.toLowerCase().match(/vsb/)) return "VSB";
    else if(reference.toLowerCase().match(/aot/)) return "AOT";
    else if (reference.split(" ").length === 2) return reference.split(" ")[0];
    else return "???";
}

const referenceANDtypeDocumentLie = function(item) {
    const refElement = item["REFERENCE"];
    const mrbRefElement = item["SOURCE TASK REFERENCE"];
    let element;
    if((refElement === undefined || refElement === "") && (mrbRefElement === undefined || mrbRefElement === "")) {
        return [
            "", "",
            "", "",
            "", "",
            "", "",
            "", "",
        ];
    }
    if(refElement === undefined || refElement === "") {
        element = mrbRefElement.split("\n");
    } else if(mrbRefElement === undefined || mrbRefElement === "") {
        element = refElement.split("\n");
    } else {
        element = [
            ...refElement.split("\n"),
            ...mrbRefElement.split("\n")
        ];
    }
    let result = element.filter(x => x.trim().length !== 0).reduce((r, v, i) => {
        if(i > 4) return r;
        else {
            const elementData = v.split(" ");
            if(elementData.length === 1) {
                if(i === 0 && (refElement !== undefined && refElement !== "")) {
                    return [
                        ...r,
                        elementData[0],
                        "AMM"
                    ];
                } else {
                    return [
                        ...r,
                        elementData[0],
                        "unknown"
                    ];
                }
            } else if(elementData.length === 2) {
                return [
                    ...r,
                    elementData[1],
                    elementData[0]
                ];
            } else {
                if(v.match(/APX|APPENDIX/)) {
                    return [
                        ...r,
                        v,
                        "MPD"
                    ]
                } else {
                    return [
                        ...r,
                        elementData.slice(1).join(" ") ,
                        elementData[0]
                    ];
                }
            }
        };
    }, []);
    while(result.length < 10) result.push("");
    // console.log(result)
    return result;
}

const numRevision = function(item) {
    return Object.values(item.slice(0,1)[0]).map(x => x.toLowerCase()).filter(x => x.includes("revision"))[0].split("\n")[0].split(":")[1].trim();
}

const dateRevision = function(item) {
    const data = Object.values(item.slice(0,1)[0]).map(x => x.toLowerCase()).filter(x => x.includes("revision"))[0].split("\n")[1].split(":")[1].trim();
    let monthNum;
    switch(data.split(" ")[0]) {
    case "jan":
        monthNum = "01";
        break;
    case "feb":
        monthNum = "02";
        break;
    case "mar":
        monthNum = "03";
        break;
    case "apr":
        monthNum = "04";
        break;
    case "may":
        monthNum = "05";
        break;
    case "jun":
        monthNum = "06";
        break;
    case "jul":
        monthNum = "07";
        break;
    case "aug":
        monthNum = "08";
        break;
    case "sep":
        monthNum = "09";
        break;
    case "oct":
        monthNum = "10";
        break;
    case "nov":
        monthNum = "11";
        break;
    case "dic":
        monthNum = "12";
        break;
    default:
        monthNum = "to-be-specifyed";
    }
    return data.split("/")[0].split(" ")[1] + "/"+monthNum+"/" + data.split("/")[1];
}

const typeModification = function(item) {
    let element = item["REV CODE"];
    if(element === undefined) return "";
    return element;
}

const endTableCommentsForSpecificCases = function(item, optimized) {
    let result = "";

    let element1 = item["ZONE"];
    if(element1 !== undefined) {
        let elements1 = element1.split("\n");
        if(elements1.length > 22) result += "(more than 22 zones)";
    }

    let element2 = item["ACCESS"];
    if(element2 !== undefined) {
        element2 = element2.replaceAll("\n", " ");
        let elements2 = element2.split(" ").filter(e => e != "");
        if(elements2.length > 50) result += "(more than 50 access)";
    }

    const refElement = item["REFERENCE"];
    const mrbRefElement = item["SOURCE TASK REFERENCE"];
    let element3;
    if((refElement !== undefined && refElement !== "") && (mrbRefElement !== undefined && mrbRefElement !== "")) {
        element3 = [
            ...refElement.split("\n"),
            ...mrbRefElement.split("\n")
        ];
    } else if(refElement === undefined || refElement === "") {
        element3 = mrbRefElement.split("\n");
    } else if(mrbRefElement === undefined || mrbRefElement === "") {
        element3 = refElement.split("\n");
    }
    if(element3.length > 5) result += "(more than 5 references)";

    const element4 = optimized ? [
        "SAMPLE THRESHOLD", "SAMPLE INTERVAL", "100% THRESHOLD", "100% INTERVAL",
        "SAMPLE THRESHOLD FH", "SAMPLE INTERVAL FH", "100% THRESHOLD FH", "100% INTERVAL FH",
        "SAMPLE THRESHOLD FC", "SAMPLE INTERVAL FC", "100% THRESHOLD FC", "100% INTERVAL FC"
    ] : [
        "SAMPLE THRESHOLD", "SAMPLE INTERVAL", "100% THRESHOLD", "100% INTERVAL"
    ];
    element4.forEach(e => {
        const value = item[e];
        if(value) {
            if(value.includes("CRR")) result += `(CRR in "${e}")`;
        }
    });

    return result;
}

